import {Component, Input, OnInit} from '@angular/core';
import {FilterService} from '../../services/filter.service';

@Component({
  selector: 'app-checkbox-group',
  templateUrl: './checkbox-group.component.html',
  styleUrls: ['./checkbox-group.component.scss']
})
export class FilterGroupComponent implements OnInit {

  @Input() group;

  collapsedView = false;
  limit = 3;

  constructor(private filter: FilterService) {
  }

  toggleList() {
    this.limit = this.limit === 3 ? this.group.items.length : 3;
  }

  toggleView() {
    this.collapsedView = !this.collapsedView;
  }

  toggleBox(name, value, group, rightCheckbox?) {
    this.filter.emitChange({name: name, value: value, group: group, rightCheckbox: rightCheckbox});
  }

  ngOnInit() {
  }

}



