import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ApiService } from '../../shared/api.service';
import { RegExpValidator } from '../../shared/regexp.validator';
import { LoginToggleViewService } from '../../shared/login-toggle-view.service';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss' ]
})

export class LoginFormComponent implements OnInit {

  public loginForm: FormGroup;
  public data: any = {username: '', password: ''};

  constructor(private fb: FormBuilder,
              private api: ApiService,
              private loginToggleView: LoginToggleViewService) {

    this.loginForm = fb.group({
      'username': new FormControl({value: null, disabled: false},
        Validators.compose([
          Validators.required,
          RegExpValidator(/@epam.com$/i)
        ])),
      'password': new FormControl({value: null, disabled: false},
        Validators.required),
    });

  }

  forgotPassword() {
    this.loginToggleView.emitChange({
      path: '/login/restore',
      data: this.data
    });
  }

  logIn() {
    this.api.checkUser(this.data);
  }

  authUser() {
    this.api.authUser();
  }

  ngOnInit() {
    this.data = this.loginToggleView.getData();
  }

}



