import {Component, OnInit, ViewEncapsulation, OnDestroy, ViewChild} from '@angular/core';
import {Challenge} from '../../shared/types';
import {ApiService} from '../../shared/api.service';
import {Subscription} from 'rxjs/Rx';
import {FilterService} from '../services/filter.service';
import {SidebarFilterComponent} from '../sidebar-filter/sidebar-filter.component';
import {EVENTS_STATUSES, EVENTS_TYPES, FILTER_GROUP_NAMES} from '../../shared/constants';

@Component({
  selector: 'app-challenges-list',
  templateUrl: './challenges-list.component.html',
  styleUrls: ['./challenges-list.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ChallengesListComponent implements OnInit, OnDestroy {

  @ViewChild(SidebarFilterComponent) sidebarFilter: SidebarFilterComponent;

  tileView = false;
  challenges: Array<Challenge> = [];
  challengesCount: { [key: string]: any } = {};
  subscription: Subscription;
  filterState: any = {
    [FILTER_GROUP_NAMES.activity.key]: {},
    [FILTER_GROUP_NAMES.level.key]: {},
    [FILTER_GROUP_NAMES.status.key]: {},
    [FILTER_GROUP_NAMES.type.key]: {},
    other: {}
  };
  search: string;
  hasFriends = false;
  showItemsPerPage = 6;
  showChallengesFrom = 0;
  showChallengesTo = this.showChallengesFrom + this.showItemsPerPage;

  constructor(private api: ApiService,
              private filter: FilterService) {


    this.subscription = this.filter.sortedCollection.subscribe( // works every time sorted collection is returned
      (collection) => {
        this.challenges = collection;
        this.setToFirstPage();
      });
  }

  countDynamicFields(challengesCount, keys, item) {
    keys.forEach(key => {
      if (!challengesCount[key]) {
        challengesCount[key] = {};
      }
      if (!challengesCount[key][item[key]]) {
        challengesCount[key][item[key]] = 0;
      }
      challengesCount[key][item[key]]++;
    });
  }

  countChallenges(challenges) {
    let that = this;
    let challengesCount = {
      [EVENTS_TYPES.teamChallenge.key]: 0,
      [EVENTS_TYPES.individual.key]: 0,
      [EVENTS_STATUSES.notStarted.key]: 0,
      [EVENTS_STATUSES.inProgress.key]: 0,
      [EVENTS_STATUSES.ended.key]: 0
    };
    let keys = [FILTER_GROUP_NAMES.activity.key, FILTER_GROUP_NAMES.level.key];
    let currentTime = new Date().getTime();
    challenges.forEach(challenge => {
      that.countDynamicFields(challengesCount, keys, challenge);
      challenge.teamChallenge ? challengesCount[EVENTS_TYPES.teamChallenge.key]++ : challengesCount[EVENTS_TYPES.individual.key]++;
      if (challenge.end - currentTime > 0) {
        if (challenge.start - currentTime > 0) {
          challenge.status = EVENTS_STATUSES.notStarted.key;
          challengesCount[EVENTS_STATUSES.notStarted.key]++;
        } else {
          challenge.status = EVENTS_STATUSES.inProgress.key;
          challengesCount[EVENTS_STATUSES.inProgress.key]++;
        }
      } else {
        challenge.status = EVENTS_STATUSES.ended.key;
        challengesCount[EVENTS_STATUSES.ended.key]++;
      }
    });
    this.challengesCount = challengesCount;
  }

  setToFirstPage() {
    this.showChallengesFrom = 0;
    this.showChallengesTo = this.showChallengesFrom + this.showItemsPerPage;
  }

  limitCollection(fromPaginator) {
    this.showChallengesFrom = fromPaginator.collectionStart;
    this.showChallengesTo = fromPaginator.collectionEnd;
  }

  resetFilters() {
    this.filterState = {
      activity: {},
      level: {},
      status: {},
      type: {},
      other: {}
    };
    this.hasFriends = false;
    this.setToFirstPage();
    this.filter.resetFilter();
  }

  resetSidebarFilter() {
    this.sidebarFilter.resetFilters();
    this.search = '';
    this.hasFriends = false;
  }

  triggerSearch(search) {
    this.search = search;
    this.setToFirstPage();
  }

  sendToFilterService(collection) {
    this.filter.emitCollection(collection);
  }

  ngOnInit() {
    this.api.getChallenges(200).subscribe(challenges => {
      this.sendToFilterService(challenges);
      this.countChallenges(challenges);
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}



