import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'formatTime'
})
export class FormatTimePipe implements PipeTransform {

  parseDifference(num) {
    num = Math.abs(num);
    if (num < 60) {
      return num.toString() + 'min';
    }
    num = Math.round(num / 60);
    if (num < 24) {
      return num.toString() + 'h';
    } else {
      return (Math.round(num / 24).toString() + 'd');
    }
  };

  transform(value: number): string {
    const current = new Date().getTime();
    let difference = value - current;
    difference = Math.round(difference / (60 * 1000));

    if (difference > 0) {
      return 'starts in ' + this.parseDifference(difference);
    } else {
      return this.parseDifference(difference) + ' ago';
    }
  }

}



