import {Component, Input, OnChanges, OnInit, Output, EventEmitter} from '@angular/core';
import {FilterGroup} from '../../shared/types';
import {EVENTS_LEVELS, EVENTS_STATUSES, EVENTS_TYPES, FILTER_GROUP_NAMES} from '../../shared/constants';

@Component({
  selector: 'app-sidebar-filter',
  templateUrl: './sidebar-filter.component.html',
  styleUrls: ['./sidebar-filter.component.scss']
})
export class SidebarFilterComponent implements OnInit, OnChanges {

  @Input() challengesCount;
  @Output() reset: EventEmitter<any> = new EventEmitter();
  @Output() searchLocation: EventEmitter<any> = new EventEmitter();

  search: string;
  // TODO: Создай сервис и подтягивай оттуда
  filterGroups: Array<FilterGroup> = [
    {
      title: FILTER_GROUP_NAMES.activity.name,
      name: FILTER_GROUP_NAMES.activity.key,
      rightCheckbox: {
        name: 'myActivities',
        title: 'my activities',
        value: false
      },
      items: []
    },
    {
      title: FILTER_GROUP_NAMES.level.name,
      name: FILTER_GROUP_NAMES.level.key,
      items: [
        {
          name: EVENTS_LEVELS.commuter.key,
          title: EVENTS_LEVELS.commuter.name,
          value: false,
          counter: 0
        },
        {
          name: EVENTS_LEVELS.hobbist.key,
          title: EVENTS_LEVELS.hobbist.name,
          value: false,
          counter: 0
        },
        {
          name: EVENTS_LEVELS.pro.key,
          title: EVENTS_LEVELS.pro.name,
          value: false,
          counter: 0
        }
      ]
    },
    {
      title: FILTER_GROUP_NAMES.status.name,
      name: FILTER_GROUP_NAMES.status.key,
      items: [
        {
          name: EVENTS_STATUSES.notStarted.key,
          title: EVENTS_STATUSES.notStarted.name,
          value: false,
          counter: 0
        },
        {
          name: EVENTS_STATUSES.inProgress.key,
          title: EVENTS_STATUSES.inProgress.name,
          value: false,
          counter: 0
        },
        {
          name: EVENTS_STATUSES.ended.key,
          title: EVENTS_STATUSES.ended.name,
          value: false,
          counter: 0
        }
      ]
    },
    {
      title: FILTER_GROUP_NAMES.type.name,
      name: FILTER_GROUP_NAMES.type.key,
      items: [
        {
          name: EVENTS_TYPES.individual.key,
          title: EVENTS_TYPES.individual.name,
          value: false,
          counter: 0
        },
        {
          name: EVENTS_TYPES.teamChallenge.key,
          title: EVENTS_TYPES.teamChallenge.name,
          value: false,
          counter: 0
        }
      ]
    }
  ];

  constructfilterGroups(challengesCount) {
    let activities = [];
    this.filterGroups.forEach(group => {
      switch (group.name) {
        case FILTER_GROUP_NAMES.activity.key:
          for (let type in challengesCount[FILTER_GROUP_NAMES.activity.key]) {
            if (challengesCount[FILTER_GROUP_NAMES.activity.key].hasOwnProperty(type)) {
              activities.push({
                name: type,
                title: type,
                value: false,
                counter: challengesCount[FILTER_GROUP_NAMES.activity.key][type]
              });
            }
          }
          break;
        case FILTER_GROUP_NAMES.level.key:
          if (challengesCount.level) {
            group.items.forEach(item => item.counter = challengesCount.level[item.name]);
          }
          break;
        case FILTER_GROUP_NAMES.status.key:
        case FILTER_GROUP_NAMES.type.key:
          group.items.forEach(item => item.counter = challengesCount[item.name]);
          break;
        default:
          break;
      }
    });
    this.filterGroups[0].items = activities;
  }

  triggerSearch() {
    this.searchLocation.emit(this.search);
  }

  resetFilters() {
    this.filterGroups.forEach(group => {
      if (group.rightCheckbox) {
        group.rightCheckbox.value = false;
      }
      group.items.forEach(name => name.value = false);
    });
    this.search = '';
    this.triggerSearch();
    this.reset.emit();
  }

  ngOnInit() {
  }

  ngOnChanges() {
    this.constructfilterGroups(this.challengesCount);
  }

}



