import {Component, OnInit} from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { RegExpValidator } from '../../shared/regexp.validator';

@Component({
  selector: 'app-password-restore-form',
  templateUrl: './password-restore-form.component.html',
  styleUrls: ['./password-restore-form.component.scss']
})
export class PasswordRestoreFormComponent implements OnInit {

  public loginForm: FormGroup;
  public data: any = {};

  constructor(private fb: FormBuilder) {

    this.loginForm = fb.group({
      'username': new FormControl({value: null, disabled: false},
        Validators.compose([
          Validators.required,
          RegExpValidator(/@epam.com$/i)
        ]))
    });

  }

  restorePassword() {
    console.log('restore password');
  }

  ngOnInit() {

  }



}



