import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { LoginToggleViewService } from '../shared/login-toggle-view.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class LoginComponent implements OnInit, OnDestroy {

  subscription: Subscription;
  private data: any = {};

  constructor(private router: Router,
              private loginToggleView: LoginToggleViewService) {

    this.subscription = loginToggleView.changeEmitted$.subscribe(
      (change) => {
        this.data = change.data;
        this.loginToggleView.emitData({
          data: this.data
        });
        this.router.navigate([change.path]);
      });

  }
  ngOnInit() {
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  navigateToLogin() {
    this.router.navigate(['/login']);
  }
}



