import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WidgetEventsComponent } from './widget-events/widget-events.component';
import { WidgetChallengesComponent } from './widget-challenges/widget-challenges.component';
import { WidgetFeedComponent } from './widget-feed/widget-feed.component';
import { WidgetTrainingsComponent } from './widget-trainings/widget-trainings.component';
import { WidgetActivityComponent } from './widget-activity/widget-activity.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { PipesModule } from '../shared/pipes/pipes.module';
import { ActivitiesModule } from '../activities/activities.module';

@NgModule({
  imports: [
    CommonModule,
    PipesModule,
    ActivitiesModule
  ],
  declarations: [
    WidgetEventsComponent,
    WidgetChallengesComponent,
    WidgetFeedComponent,
    WidgetTrainingsComponent,
    WidgetActivityComponent,
    DashboardComponent
  ]
})
export class DashboardModule { }



