import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'challengesFriends',
  pure: false
})
export class ChallengesFriendsPipe implements PipeTransform {

  transform(array: any, hasFriends: boolean): any {
    if (hasFriends) {
      return array.filter( item => (item.friends > 0) );
    } else {
      return array.filter( item => true );
    }
  }

}



