interface User {
  name: string;
  avatar?: string;
}
interface Like {
  time: number;
  user: User;
}
interface Comment extends Like {
  comment: string;
}
export interface Activity {
  user: User;
  eventType: string;
  time: number;
  likes: Array<Like>;
  comments: Array<Comment>;
}

export interface Challenge {
  id: number;
  title: string;
  desc: string;
  location: string;
  participants: number;
  friends: number;
  start: number;
  end: number;
  registration: number;
  teamChallenge: boolean;
  joined: boolean;
  categoryId: string;
  level: string;
  status?: string;
}

interface FilterGroupItem {
  name: string;
  title: string;
  value: boolean;
  counter?: number;
}

export interface FilterGroup {
  name: string;
  title: string;
  rightCheckbox?: FilterGroupItem;
  items: Array<FilterGroupItem>;
}



