import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ChallengesComponent } from './challenges.component';
import { ChallengesListComponent } from './challenges-list/challenges-list.component';
import { MyChallengesComponent } from './my-challenges/my-challenges.component';
import { TileViewChallengeComponent } from './tile-view-challenge/tile-view-challenge.component';
import { RowViewChallengeComponent } from './row-view-challenge/row-view-challenge.component';
import { LoggedInGuard } from '../app.guard';
import { SidebarFilterComponent } from './sidebar-filter/sidebar-filter.component';
import { FilterGroupComponent } from './sidebar-filter/checkbox-group/checkbox-group.component';
import { FormsModule } from '@angular/forms';
import { FilterService } from './services/filter.service';
import { PipesModule } from '../shared/pipes/pipes.module';
import { SharedModule } from '../shared/shared.module';


const routes: Routes = [
  { path: 'challenges', component: ChallengesComponent, canActivate: [LoggedInGuard], children: [
    { path: 'list', component: ChallengesListComponent },
    { path: '', component: MyChallengesComponent }
  ] },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    CommonModule,
    FormsModule,
    PipesModule,
    SharedModule
  ],
  declarations: [
    ChallengesComponent,
    ChallengesListComponent,
    MyChallengesComponent,
    TileViewChallengeComponent,
    RowViewChallengeComponent,
    SidebarFilterComponent,
    FilterGroupComponent
  ],
  exports: [
    RouterModule,
    ChallengesListComponent,
    MyChallengesComponent
  ],
  providers: [ FilterService ]
})

export class ChallengesModule { }



