import {Component, OnInit} from '@angular/core';
import {Activity} from '../../shared/types';
import {WidgetActivityService} from '../widget-activity/widget-activity.service';


@Component({
  selector: 'app-widget-feed',
  templateUrl: './widget-feed.component.html',
  styleUrls: ['./widget-feed.component.scss'],
  providers: [WidgetActivityService]
})

export class WidgetFeedComponent implements OnInit {

  public activities: Array<Activity> = this.widgetActivityService.getActivities();

  constructor(private widgetActivityService: WidgetActivityService) { }

  ngOnInit() {

  }
}



