import {Component, EventEmitter, Input, OnChanges, OnInit, Output} from '@angular/core';


@Component({
  selector: 'app-paginator',
  templateUrl: './paginator.component.html',
  styleUrls: ['./paginator.component.scss']
})
export class PaginatorComponent implements OnInit, OnChanges {

  @Input() collection;
  @Input() showChallengesFrom;
  @Input() showItemsPerPage;
  @Output() limitCollection: EventEmitter<any> = new EventEmitter();

  currentPage = 1;
  pages: number;
  pagesArray = [];
  collectionStart = 0;
  collectionEnd = this.showItemsPerPage;
  sliceBtnsFrom = 0;
  sliceBtnsTo = 5;

  refreshView() {
    this.collectionStart = this.showItemsPerPage * (this.currentPage - 1);
    this.collectionEnd = this.collectionStart + this.showItemsPerPage;
    if (this.pages > 5) { // happens when there are more than 5 pages
      if (this.currentPage <= 2) { // if page 1 or 2 selected
        this.sliceBtnsFrom = 0; // then it doesn't make shift in the button array
        this.sliceBtnsTo = 5; // it sets the button array at its start
      }
      if (this.currentPage > 2) { // if pages from middle selected
        this.sliceBtnsFrom = this.currentPage - 3; // then is moves the button array so that the current page could be right in the middle
        this.sliceBtnsTo = this.currentPage + 2;
      }
      if (this.currentPage >= this.pages - 2) { // if the last or previous to the last page selected
        this.sliceBtnsFrom = this.pages - 5; // then it doesn't make shift in in the button array
        this.sliceBtnsTo = this.pages; // it sets the button array at its end
      }
    } else { // happens when there are less than 5 pages
      this.sliceBtnsFrom = 0; // it shows the entire button array
      this.sliceBtnsTo = this.pages;
    }
    this.limitCollection.emit({collectionStart: this.collectionStart, collectionEnd: this.collectionEnd});
  }

  changePage(num) {
    if (num === 0) {
      // this.currentPage = this.pages;
      // this.refreshView();
      return;
    }
    if (num > this.pages) {
      // this.currentPage = 1;
      // this.refreshView();
      return;
    }
    this.currentPage = num;
    this.refreshView();
  }

  ngOnChanges() {
    if (this.showChallengesFrom === 0) {
      this.currentPage = 1;
    }
    this.pages = Math.ceil(this.collection.length / this.showItemsPerPage);
    this.pagesArray = Array(this.pages).fill('').map((x, i) => i);
    if (this.pages > 0 && this.currentPage > this.pages) {
      this.currentPage = this.pages;
      this.collectionStart = this.pages - this.showItemsPerPage;
      this.collectionEnd = this.pages;
    }
    this.refreshView();
  }

  ngOnInit() {

  }

}



