import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Router } from '@angular/router';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Observable';
import { Challenge } from './types';

@Injectable()

export class ApiService {

  private URL = 'http://ext.epm-wbc.projects.epam.com:8082/';
  challenges = [];

  constructor(private http: Http,
              private router: Router) {
  }

  authUser() {
    this.http.get(this.URL + 'user')
      .map((res: Response) => res.json()).subscribe(
        (res: any) => { console.log('response', res); },
        err => { console.log('error', err); }
      );
  }

  checkUser(data) {
    this.http.get('http://localhost:3000/users?Username=' + data.username + '&Password=' + data.password)
      .map((res: Response) => res.json()).subscribe(
      (res: any) => {
        if (res.length < 1) { alert('Go away, you are not welcome here!'); return false; }
        alert('Welcome, ' + res[0].Username);
        localStorage.setItem('user', res[0].Username);
        this.router.navigate(['']);
      },
      err => {
        console.log(err);
      }
    );
  }

  getChallenges(num): Observable<Challenge[]> {
    let challenges = this.http.get('http://localhost:3000/challenges?_limit=' + num)
      .map((res: Response) => res.json());
    return challenges;
  }

}



