import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login.component';
import { PasswordRestoreFormComponent } from './password-restore-form/password-restore-form.component';
import { LoginFormComponent } from './login-form/login-form.component';
import { LogoComponent } from '../shared/components/logo/logo.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { LoginToggleViewService } from '../shared/login-toggle-view.service';

const routes: Routes = [
  { path: 'login', component: LoginComponent, children: [
    { path: 'restore', component: PasswordRestoreFormComponent },
    { path: '', component: LoginFormComponent }
  ] }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    CommonModule,
    FormsModule,
    ReactiveFormsModule
  ],
  declarations: [
    LoginComponent,
    LogoComponent,
    PasswordRestoreFormComponent,
    LoginFormComponent
  ],
  exports: [
    RouterModule,
    LoginComponent,
    LogoComponent,
    PasswordRestoreFormComponent,
    LoginFormComponent
  ],
  providers: [LoginToggleViewService]
})

export class LoginModule { }



