import {Injectable} from '@angular/core';
import {Subject} from 'rxjs/Subject';
import {FILTER_GROUP_NAMES} from '../../shared/constants';
import {Challenge} from '../../shared/types';

@Injectable()

export class FilterService {

  collection: Array<Challenge>;
  filterState: any = {
    [FILTER_GROUP_NAMES.activity.key]: {},
    [FILTER_GROUP_NAMES.level.key]: {},
    [FILTER_GROUP_NAMES.status.key]: {},
    [FILTER_GROUP_NAMES.type.key]: {},
    other: {}
  };
  filterBy: Array<string> = [
    FILTER_GROUP_NAMES.activity.key,
    FILTER_GROUP_NAMES.level.key,
    FILTER_GROUP_NAMES.status.key
  ];

  private emitSortedCollection = new Subject<any>();
  public sortedCollection = this.emitSortedCollection.asObservable();

  checkIfHasTrue(object) {
    for (let field in object) {
      if (object[field]) {
        return true;
      }
    }
  }

  resetFilter() {
    this.filterState = {
      [FILTER_GROUP_NAMES.activity.key]: {},
      [FILTER_GROUP_NAMES.level.key]: {},
      [FILTER_GROUP_NAMES.status.key]: {},
      [FILTER_GROUP_NAMES.type.key]: {},
      other: {}
    };
    this.sendSortedCollection();
  }

  emitChange(change: any) {
    if (!change.rightCheckbox) {
      this.filterState[change.group][change.name] = change.value;
    } else {
      this.filterState.other[change.name] = change.value;
    }
    this.sendSortedCollection();
  }

  sendSortedCollection() {
    let sortedCollection = this.collection;
    let that = this;

    if (this.checkIfHasTrue(this.filterState.other)) {
      if (that.filterState.other.myActivities) {
        sortedCollection = sortedCollection.filter(item => (item.joined));
      }
    }

    this.filterBy.forEach(sortField => {
      if (this.checkIfHasTrue(this.filterState[sortField])) {
        let sortingFields = Object.keys(this.filterState[sortField]);
        sortedCollection = sortedCollection.filter(item => {
          let boolArray: Array<boolean> = [];
          sortingFields.forEach(function (field) {
            boolArray.push(that.filterState[sortField][field] && item[sortField] === field);
          });
          return (boolArray.some(elem => elem));
        });
      }
    });

    if (this.checkIfHasTrue(this.filterState.type)) {
      if (!(that.filterState.type.individual && that.filterState.type.teamChallenge)) {
        sortedCollection = sortedCollection.filter(
          item => !that.filterState.type.individual || (that.filterState.type.individual && !item.teamChallenge)
        ).filter(
          item => !that.filterState.type.teamChallenge || (that.filterState.type.teamChallenge && item.teamChallenge)
        );
      }
    }
    this.emitSortedCollection.next(sortedCollection);
  }

  emitCollection(collection: any) {
    this.collection = collection;
    this.sendSortedCollection();
  }

}



