import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TrainingComponent } from './training/training.component';
import { FriendsComponent } from './friends/friends.component';
import { DashboardComponent } from './dashboard/dashboard/dashboard.component';
import { LoggedInGuard } from './app.guard';

const routes: Routes = [
  { path: '', component: DashboardComponent, canActivate: [LoggedInGuard] },
  { path: 'training', component: TrainingComponent, canActivate: [LoggedInGuard] },
  { path: 'friends', component: FriendsComponent, canActivate: [LoggedInGuard] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



